.app__contact{
  flex: 1;
  width: 100%;
  flex-direction: column;
  background-color: #252934;
}

.divider {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  background-color: #3d404c;
}

.Contact__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0;
}

.Contact__container div {
  max-width: 1400px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.Contact__container p {
  padding: 10px 0;
}

.Contact__whatsapp {
  display: flex;
  padding-right: 10px;
  align-items: center;
}

.Contact__whatsapp p {
  padding-left: 10px;
}
