    transform: translateY(0%);
  }
}

.Header__headline h1 {
  color: #fff;
  font-size: 2em;
  text-shadow: 0 0 2px #000;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}

.Header__headline h2 {
  color: #00b8e4;
  font-size: 1.2em;
  text-shadow: 0 0 2px rgb(32, 28, 28);
  font-family: "Montserrat";
  text-transform: uppercase;
}

.Header__skills {
  display: flex;
  align-items: center;
}

.Header__skills img {
  height: 1em;
  width: 1em;
  margin: 0px 0.2em 0px 0px;
}

.img-box {
  width: 90%;
