body,
h1,
h2,
p,
ul,
li,
a {
  margin: 0px;
  padding: 0px;
}

a {
  text-decoration: none;
}

body {
  font-family: "Nunito", Helvetica, Arial, sans-serif;
  color: #f8f4f4;

  background-color: #171c28;
}

ul,
li {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

* {
  box-sizing: border-box;
}

