@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;1,100&display=swap");

.Header__header {
  display: flex;
  z-index: 1000;
  position: fixed;
  top: 0;
  width: 100%;
  transition: all 0.3s ease;
  height: 60px;
}

.Header__header div {
  max-width: 1400px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 10px 20px;
}

.hide {
  top: -63px;
}

.background-transparency {
  background-color: rgba(0, 0, 0, 0.92);
  background-color: #1f1e1e;
  box-shadow: 0 2px 5px 0 rgb(0 0 0 / 12%), 0 2px 1px 0 rgb(0 0 0 / 10%);
}

.Header__header ul {
  display: flex;
  align-items: center;
  padding-left: 10px;
}

.Header__header ul li {
  padding-left: 10px;
}

.Header__logo {
  max-width: 60px;
  min-width: 60px;
}

.social-networks-items svg:hover {
  opacity: 0.6;
  cursor: pointer;
}

.Header__logo_link {
  transition: opacity 0.3s ease-in-out;
  color: #fff;
  font-weight: 700;
  font-size: 28px;
  text-decoration: none;
  letter-spacing: 2px;
  padding: 0px 20px;
  font-family: neuzeit grotesk bold, sans-serif;
}

.Header__post-image {
  background-image: url("../../images/cel-bg.jpg");
  background-position: center;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.Header__img-color-overlay {
  background-color: rgba(8, 12, 34, 0.6);
}

.Header__headline {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1140px;
  margin: 0 auto;
  min-height: 100vh;
  flex-wrap: nowrap;
}

.Header__headline_box {
  animation: show_Header__headline_box 2.5s ease-out forwards;
}

@keyframes show_Header__headline_box {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

.Header__headline h1 {
  color: #fff;
  font-size: 2em;
  text-shadow: 0 0 2px #000;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
}

.Header__headline h2 {
  color: #00b8e4;
  font-size: 1.2em;
  text-shadow: 0 0 2px rgb(32, 28, 28);
  font-family: "Montserrat";
  text-transform: uppercase;
}
.Header__skills {
  display: flex;
  align-items: center;
}

.Header__skills img {
  height: 1em;
  width: 1em;
  margin: 0px 0.2em 0px 0px;
}

.img-box {
  width: 90%;
  position: relative;
  top: 10px;
  right: -30px;
  animation: show_img-box 2.5s ease-out forwards;
  height: 500px;
  /* height: 100%; */
  background-image: url("../../images/vscode.png");
}

.img-vscode {
  display: none;
}

@media (max-width: 1190px) {
  .Header__headline_box {
    margin-left: 20px;
  }

  .img-box {
    width: 70%;
    top: 10px;
    right: 0px;
  }
}

@supports (-webkit-touch-callout: none) {
  /* CSS specific to iOS devices */
  .Header__post-image {
    background-attachment: scroll;
  }
}

@media (max-width: 940px) {
  .Header__header div {
    /* padding: 10px 10px; */
  }

  

  .Header__logo_link {
    padding: 0px 0px;
  }

  .Header__headline {
    flex-wrap: wrap;
  }

  .img-box {
    background-image: none;
    width: 100%;
    margin: 50px 30px;
    height: auto;
  }

  .img-vscode {
    display: block;
  }

  .profile_photo {
    display: flex;
    justify-content: center;
  }

  .Header__headline {
    padding-top: 50px;
  }
}

@keyframes show_img-box {
  0% {
    opacity: 0;
    transform: translateX(50%);
  }
  50% {
    opacity: 0;
    transform: translateX(50%);
  }
  100% {
    opacity: 1;
    transform: translateX(0%);
  }
}

.profile_photo {
  width: 80%;
  /* border-radius: 50%; */
  /* margin-left: 100px; */
  /* border-radius: 50%; */
}

.section {
  /* position: relative; */
  /* height: 100vh; */
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-shadow: 0 0 5px #000;
}
